.restaurant-form-input-box{
  height: 1.7rem;
  border: 1.8px #E7E7E7 solid;
  border-radius: 5px;
  padding: 0 10px;
}

.restaurant-form-input-box::placeholder{

  font-size: 15px;
}

.create-restaurant-form-step-1-third-row, .create-restaurant-form-step-1-fourth-row{
  margin-top: 12px;
}

.form-step-1-pic{
  margin-top: 20px;
}

.create-restaurant-title-container{
  width: 100%;
  background-color: #F3F4F6;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.back-to-restaurants-button{
  position: absolute;
  left: 0px;
  /* background-color: lightblue; */
  display: flex ;
  align-items: center;
  height: 100%;
  color: lightcoral;
  font-size: 16px;
}

.create-restaurant-progress-bar-container{
  width: 450px;
  height:7px;
  background-color: #dbdbdb;
}

.create-restaurant-form-footer{
  /* background-color: lightblue; */
  display:flex;
  justify-content: space-between;
  width: 40em;
  margin: auto
}

.create-restaurant-progress-bar{
  height: 100%;
  background-color: lightsalmon;
  align-self: flex-start;
  /* justify-self: flex-start; */
}


.create-restaurant-form-step-1-container{
  /* background-color: lightcyan; */
  /* height: 600px; */
  height:fit-content;
  width: 40em;
  margin: 20px auto;
}

.create-restaurant-form-step-1-first-row{
  /* background-color: goldenrod; */
  display: flex;
  justify-content: space-around;
  align-items: center;

}

.create-restaurant-label-input-container{
  margin-top: 0.5em;
}

/* REMOVED MARGIN TOP FOR THE ORDER CONFIRM PAGE - classname shared with create restaurant */
.create-restaurant-address-dropdown{
  /* margin-top: 0.5em; */
  height: 2.8em;
}

.create-restaurant-label-input-container{
  display:flex;
  flex-direction: column;
  align-items: flex-start;

}

.create-restaurant-label-input-container.top-row{
  align-self: flex-start;
  justify-self: flex-start;
  /* margin-left: -139.5px; */
  margin-left: -8em;

}

.create-restaurant-form-step-1-third-row{
  display:flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

}

.create-restaurant-label-input-container.third-row{
  align-self: flex-start;
  justify-self: flex-start;
  margin-left: -96.5px;

}

.one-input-per-row{
  width: 100%;
  box-sizing: border-box;
}

.create-restaurant-form-step-1-fourth-row{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
}

.create-restaurant-form-step-1-left-container, .create-restaurant-form-step-1-right-container{
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: fit-content;
  /* background-color: lightsalmon; */
}



.create-restaurant-form-restaurant-pic, .restaurant-form-restaurant-logo{
  width: 100%;
  height: 300px;
  min-height: 300px;
  object-fit: cover;
  box-sizing: border-box;
}


/* -------------------------------------------------------- */
/* form step #2 */
.create-restaurant-form-step-2-container{
  height: fit-content;
  width: 40em;
  margin: 20px auto;
  /* background-color: lightblue; */

}

.form-step-2-label-input-container{
  display:flex;
  /* background-color: lightblue; */
  flex-direction: column;
  align-items: flex-start;

}

.form-step-2-label-input-container.left-box{
  margin-right: 15em;
}

.form-step-2-top-bottom-row{
  display: flex;
  justify-content: flex-start;
  /* background-color: lightcoral; */
}

.form-step-2-input{
  width: 200px;
  box-sizing: border-box;
  height: 2.4em;
}

/* .restaurant-form-input-box.restaurant-form-select{
  color: rgb(118, 118, 118);
} */

.create-restaurant-button{
  border: none;
  background-color: lightcoral;
  color: white;
  border-radius: 5px;
  display:flex;
  justify-content: center;
  align-items: center;
  height: 32px;
  width: 57px;
}

.create-restaurant-button:hover{
  background-color:#d87373;
  box-shadow: transparent 0px 0px 0px 1px inset;
  cursor: pointer
}


button:disabled {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
  pointer-events: none;
}


/* ---------------------------------------------------- */
/* form step 3 */
.form-step-3-label-container{
  display:flex;
  flex-direction: column;


}

.create-restaurant-form-step-3-container{
  width: 40em;
  margin: 20px auto;
}

.form-step-3-row{
  display:flex;
  flex-direction: row;
  justify-content: flex-start;
}

.validation-errors-container{
  width: 40em;
  margin:auto;
  display: flex;
  justify-content: flex-start;
  /* back */
}
